import React from 'react'
import DocLayout from '../../../components/layout/doc';

export default () => {

    return (
        <DocLayout>

            <h2>Navbar</h2>

            <div className='background-white padding-1'>

                <nav className='navbar background-primary'>

                    <div className='navbar-start'>
                        <div className='navbar-item'>item left</div>
                    </div>
                    <div className='navbar-end'>
                        <div className='navbar-item'>item right</div>
                    </div>

                </nav>
            </div>

        </DocLayout>
    )
}