import React, { useState } from 'react'
import { Link } from '@reach/router';

const Caret = ({
    open = false
}) => (
        <i className='fas fa-caret-left margin-left-1' style={{ transition: 'transform 50ms', transform: open ? 'rotate(-90deg)' : '', float: 'right' }}></i>
    )

const MenuMainItem = ({ title, children }) => {

    const [open, setOpen] = useState(false)

    return (

        <li className='margin-bottom-1'>

            <div onClick={() => { setOpen(!open) }} className='cursor-pointer padding-left-1 padding-right-1' style={{ background: 'rgba(0,0,0,0.2)' }}>
                {title} <Caret open={open} />
            </div>

            {open && <p>
                {children}
            </p>}

        </li>
    )

}

const Menu = () => {

    return (
        <nav>
            <ul className='no-style'>

                <MenuMainItem title='Getting started'>
                    ici
                </MenuMainItem>

                <MenuMainItem title='Good practices'>
                    ici
                </MenuMainItem>

                <MenuMainItem title='Components'>
                    <ul className='no-style'>
                        <li><Link to='/docs/components/navbar'>Navbar</Link></li>
                        <li><Link to='/docs/components/hero'>Hero</Link></li>
                        <li><Link to='/docs/components/progress'>Progress</Link></li>
                        <li><Link to='/docs/components/notification'>Notification</Link></li>
                    </ul>
                </MenuMainItem>

                <MenuMainItem title=' Variables'>
                    ici
                </MenuMainItem>

            </ul>
        </nav>
    )
}

const DocLayout = ({ children, style = {}, className = '' }) => {

    return (

        <div className='columns'>

            <div className='column background-primary' style={{ width: 400, flex: 'none', height: '100vh', overflowY: 'auto' }}>

                <div className='padding-1'>
                    <Menu />
                </div>
            </div>
            <div className='column' style={{ height: '100vh', overflowY: 'auto' }}>
                <div className='padding-1'>

                    {children}
                </div>
            </div>

        </div>

    )
}

export default DocLayout;